import { useEffect, useState } from 'react';
import { MultiStopwatchUtils } from './multiStopwatch';

const STORAGE_KEY = 'stopwatch-list';
const NEXT_ID_KEY = 'next-id';

interface StopwatchManager {
  stopwatchIds: string[];
  addStopwatch: () => void;
  removeStopwatch: (id: string) => void;
}

export const useStopwatchManager = (): StopwatchManager => {
  const [stopwatchList, setStopwatchList] = useState<string[]>([]);
  const [nextId, setNextId] = useState(1);

  useEffect(() => {
    const storedStopwatchList = localStorage.getItem(STORAGE_KEY);
    const storedNextId = localStorage.getItem(NEXT_ID_KEY);

    if (storedStopwatchList) {
      setStopwatchList(JSON.parse(storedStopwatchList));
    }

    if (storedNextId) {
      setNextId(JSON.parse(storedNextId));
    }
  }, []);

  const saveToLocalStorage = (ids: string[], nextId: number) => {
    localStorage.setItem(NEXT_ID_KEY, JSON.stringify(nextId));
    localStorage.setItem(STORAGE_KEY, JSON.stringify(ids));
  };

  const save = (ids: string[], nextId: number) => {
    setStopwatchList(ids);
    setNextId(nextId);
    saveToLocalStorage(ids, nextId);
  };

  const addStopwatch = () => {
    const newMultiStopwatch = MultiStopwatchUtils.createEmpty(nextId.toString());

    localStorage.setItem(nextId.toString(), JSON.stringify(newMultiStopwatch));

    save([...stopwatchList, nextId.toString()], nextId + 1);
  };

  const removeStopwatch = (id: string) => () => {
    localStorage.removeItem(id);
    save(
      stopwatchList.filter(sw => sw !== id),
      nextId,
    );
  };

  return { stopwatchIds: stopwatchList, addStopwatch, removeStopwatch };
};
