import { MultiStopwatch, Stopwatch } from 'utils';
import * as XLSX from 'xlsx-js-style';

const allBorders = (style: string) => ({
  top: { style },
  bottom: { style },
  left: { style },
  right: { style },
});

const NameCell = (name: string) => ({
  v: name,
  t: 's',
  s: {
    font: { sz: 14, bold: true, name: 'Liberation Sans' },
    alignment: { vertical: 'center', horizontal: 'center' },
  },
});

const NotesCell = (notes: string) => ({
  v: notes,
  t: 's',
  s: {
    font: { sz: 8, name: 'Liberation Sans' },
    alignment: { vertical: 'top' },
  },
});

const HeaderCell = (text: string | number) => ({
  v: text,
  t: 's',
  s: {
    font: { sz: 12, bold: true, name: 'Liberation Sans' },
    alignment: { vertical: 'center', horizontal: 'center' },
    border: allBorders('thin'),
    fill: { fgColor: { rgb: 'dddddd' } },
  },
});

const DataLargeCell = (text: string | number) => ({
  v: text,
  s: {
    font: { sz: 12, name: 'Liberation Sans' },
    alignment: { vertical: 'center', horizontal: 'center' },
    border: allBorders('thin'),
  },
});

const SplitTimeCell = (time: number) => ({
  v: time,
  s: {
    font: { sz: 12, name: 'Liberation Sans' },
    alignment: { vertical: 'center', horizontal: 'center' },
    border: {
      left: { style: 'thin' },
      right: { style: 'thin' },
      top: { style: 'thin' },
    },
  },
});

const LapTimeCell = (time: number) => ({
  v: time,
  s: {
    font: { sz: 10, name: 'Liberation Sans' },
    alignment: { vertical: 'center', horizontal: 'center' },
    border: {
      left: { style: 'thin' },
      right: { style: 'thin' },
      bottom: { style: 'thin' },
    },
  },
});

export default (ms: MultiStopwatch) => {
  const maxLaps = Math.max(...ms.stopwatches.map(s => s.laps.length));

  const workbook = XLSX.utils.book_new();

  const worksheet = XLSX.utils.aoa_to_sheet([
    [NameCell(ms.name), '', '', NotesCell(ms.notes)],
    [],
    [
      HeaderCell('Name'),
      HeaderCell('Final Time'),
      HeaderCell('Laps'),
      ...Array.from({ length: maxLaps - 1 }).map((_, i) => HeaderCell(i + 1)),
    ],
    [
      HeaderCell(''),
      HeaderCell(''),
      ...Array.from({ length: maxLaps }).map((_, i) => HeaderCell(i + 1)),
    ],
    ...ms.stopwatches.map(s => stopwatchToArray(s)).flat(),
  ]);

  worksheet['!merges'] = [
    createMerge(0, 0, 2, 3), // MultiStopwatch.name
    createMerge(0, 3, 2, 4), // MultiStopwatch.notes
    createMerge(2, 0, 2), // Name Label
    createMerge(2, 1, 2), // Final Time Label
    createMerge(2, 2, 1, maxLaps), // Laps Label
    // Stopwatches
    ...ms.stopwatches
      .map((s, index) => [
        // Stopwatch.name
        createMerge(index * 2 + 4, 0, 2),
        // Final Time
        createMerge(index * 2 + 4, 1, 2),
      ])
      .flat(),
  ];

  worksheet['!cols'] = Array.from({ length: Math.max(maxLaps + 2, 7) }).map(_ => ({ width: 13 }));

  XLSX.utils.book_append_sheet(workbook, worksheet, ms.name);

  return workbook;
};

const createMerge = (row: number, col: number, rowSpan = 1, colSpan = 1) => ({
  s: { r: row, c: col },
  e: { r: row + rowSpan - 1, c: col + colSpan - 1 },
});

const stopwatchToArray = (s: Stopwatch) => {
  const row1: any[] = [DataLargeCell(s.name), DataLargeCell(s.stopTime - s.startTime)];
  const row2: any[] = [DataLargeCell(''), DataLargeCell('')];

  s.laps.map((lap, index, arr) => {
    row1.push(SplitTimeCell(lap - s.startTime));
    row2.push(LapTimeCell(index ? lap - arr[index - 1] : lap - s.startTime));
  });

  return [row1, row2];
};
